import React, { Component } from 'react'

export default class About extends Component {
    render() {
        return (
            <div>
                <section className="colorlib-about" data-section="about">
                    <div className="colorlib-narrow-content">
                        <div className="row">
                            <div className="col-md-12">
                                <div className="row row-bottom-padded-sm animate-box" data-animate-effect="fadeInLeft">
                                    <div className="col-md-12">
                                        <div className="about-desc">
                                            <span className="heading-meta">About Me</span>
                                            <h2 className="colorlib-heading">Who Am I?</h2>
                                            <p>I am a senior year Mechanical Engineering student and will graduate in May 2020 from Lehigh University.
                                                I work on Solid Mechanics related research under the supervision of Prof. Herman F. Nied.
                                                Besides, I have been the tutor for MECH12 (Strength of Materials) and ME104 (Thermodynamics) courses and enjoy sharing ideas with others.
                                            </p>
                                            <p>For personal interests, I an aviation lover and love to take aircraft photos. 
                                                With the introduction of new generation aircraft (Boeing 787 and Airbus 350), I have great curiosity in composite materials.
                                            </p>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
        )
    }
}
