import React, { Component } from 'react'

export default class Projects extends Component {
	render() {
		return (
			<div>
				<section className="colorlib-work" data-section="projects">
					<div className="colorlib-narrow-content">
						<div className="row">
							<div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
								<span className="heading-meta">Memorable moments</span>
								<h2 className="colorlib-heading animate-box">Photos</h2>
							</div>
						</div>
						<div className="row">
							<div className="col-md-4 animate-box" data-animate-effect="fadeInLeft">
								<div className="project" style={{ backgroundImage: 'url(images/family.jpg)' }}>
									<div className="desc">
										<div className="con">
											<h3><a href="#" target="_blank" rel="noopener noreferrer">my family</a></h3>
											<span>my parents and I in California</span>
											<span>As an international student, I always miss my parents</span>
											
										</div>
									</div>
								</div>
							</div>
							<div className="col-md-4 animate-box" data-animate-effect="fadeInRight">
								<div className="project" style={{ backgroundImage: 'url(images/gaf.jpg)' }}>
									<div className="desc">
										<div className="con">
										<h3><a href="#" target="_blank" rel="noopener noreferrer">my GAF research project</a></h3>
											<span>The fastener I design to test the mechanical behavior of GAF roof system.</span>
											<span>As my instructors, Prof. Nied and Dr. Yi helped me learn a lot on experimental side of solid mechanics </span>
											
										</div>
									</div>
								</div>
							</div>
							<div className="col-md-4 animate-box" data-animate-effect="fadeInTop">
								<div className="project" style={{ backgroundImage: 'url(images/350.jpg)' }}>
									<div className="desc">
										<div className="con">
										<h3><a href="#" target="_blank" rel="noopener noreferrer"> CX Airbus A350 at HKG airport </a></h3>
											<span>It's a pleasure to take Cathay Pacific newest A350 to go back home.</span>
											<span>A350 is quiet and comfortable for passagers! </span>
												
										</div>
									</div>
								</div>
							</div>
						</div>
					</div>
				</section>
			</div>
		)
	}
}
